import { useCallback, useState } from "react";
import { useMutation, useModalVisibility } from "../../hooks";
import createNotification from "../../notification";
import { RecordWithAnyData, useTableControlsButtonsType } from "../../types";

const useTableControlsButtons = ({
  apiId,
  runFetch,
}: useTableControlsButtonsType) => {
  const [selectedRecord, setSelectedRecord] = useState<RecordWithAnyData>({});
  const { visible, handleClose, handleOpen } = useModalVisibility();

  const { mutate, loading } = useMutation({
    apiId,
    method: "POST",
  });

  const handleAdd = useCallback(() => {
    handleOpen();
    setSelectedRecord({ query_status: "n" });
  }, [handleOpen]);

  const handleEdit = useCallback(() => {
    handleOpen();
    setSelectedRecord({ ...selectedRecord, query_status: "u" });
  }, [handleOpen, selectedRecord]);

  const handleDelete = useCallback(() => {
    mutate({
      data: { ...selectedRecord, query_status: "d" },
      cb: ({ error }) => {
        error && createNotification("error", error.message);
        if (!error) {
          runFetch && runFetch();
        }
      },
    });
  }, [mutate, runFetch, selectedRecord]);

  return {
    selectedRecord,
    setSelectedRecord,
    visible,
    handleClose,
    handleAdd,
    handleEdit,
    handleDelete,
    handleSaveRecord: mutate,
    loading,
  };
};

export default useTableControlsButtons;
