import { memo } from "react";
import { PaginationContainer, PageButton, TotalRecordView } from "./style"
import { PaginationProps } from "./index.interface"

const Pagination = ({ currentPage, onPageChange, totalRecord, step }: PaginationProps) => {

  const totalPages = Math.ceil(totalRecord / step);
  const pageNumbers = Array.from({ length: totalPages }, (_, index) => index + 1);

  return (
    <PaginationContainer>
      {pageNumbers.length !== 1 && pageNumbers.map(number => (
        <PageButton
          key={number}
          active={number === currentPage}
          onClick={() => onPageChange(number)}
          disabled={number === currentPage}
        >
          {number}
        </PageButton>
      ))}
      <TotalRecordView>
        {`total Records is ${totalRecord}`}
      </TotalRecordView>
    </PaginationContainer>
  );
};

export default memo(Pagination);