import { memo, useCallback } from "react";
import { useTranslateId } from "../translation-layer";
import { StyledCheckBox } from "./style";
import { CheckBoxProps } from "./interface";

const CheckBox = ({
  label = "",
  checked,
  onChange,
  name,
  width,
  height,
  disabled,
  required,
  margin,
}: CheckBoxProps) => {
  const handleChange = useCallback(
    (event: { target: { checked: boolean } }) => {
      onChange({ name: name, value: event.target.checked });
    },
    [name, onChange]
  );

  const translate = useTranslateId();

  return (
    <StyledCheckBox
      onChange={handleChange}
      checked={checked}
      width={width}
      height={height}
      disabled={disabled}
      margin={margin}
      required={required}
    >
      {translate(label)}
    </StyledCheckBox>
  );
};

export default memo(CheckBox);
