import {
  memo,
  useState,
  useCallback,
  useImperativeHandle,
  forwardRef,
  useMemo,
} from "react";
import { useFetch, useEffectTimeOut, useModalVisibility } from "../hooks";
import ConfirmationModal from "../confirmation-modal";
import { Table } from "../table";
import { usePrivilegeLayer } from "../privilege-layer";
import {
  TableWithApiProps,
  TableForwardedRefType,
  RecordWithAnyData,
} from "../types";

const TableWithApi = (
  {
    apiId,
    columns,
    ModalContent,
    onClick,
    fetchOnFirstRun = false,
    params,
    checkForParams = false,
    modalWidth = "60%",
    printProps,
    reportName,
    disableAutoRefresh,
    handleDelete,
    handleAdd,
    handleEdit,
    canAdd,
    canDelete,
    canEdit,
    canPrint,
    usePrivilege = true,
    ...tableProps
  }: TableWithApiProps,
  ref: TableForwardedRefType
) => {
  const { visible, handleClose, handleOpen } = useModalVisibility();
  const [data, setData] = useState<RecordWithAnyData[]>([]);
  const [totalRecord, setTotalRecords] = useState(0);

  const {
    can_add,
    can_edit,
    can_delete,
    can_print,
    pagination,
    refresh_time_out,
  } = usePrivilegeLayer();

  const handleFetchTableData = useCallback((response: RecordWithAnyData) => {
    const { data, total_records } = response || {};
    setData(data);
    setTotalRecords(total_records);
  }, []);

  const { runFetch, loading, previousParams } = useFetch({
    apiId,
    fetchOnFirstRun,
    params: {
      ...params,
      current_page: 1,
      page_step: pagination,
    },
    checkForParams,
    onResponse: handleFetchTableData,
  });

  const foundDataSource = useMemo(() => data, [data]);

  useImperativeHandle(ref, () => ({
    runFetch,
    setTableData: setData,
    resetTableData: () => {
      setData([]);
    },
    getCurrentDataSource: () => foundDataSource,
  }));

  useEffectTimeOut(() => {
    fetchOnFirstRun && !disableAutoRefresh && runFetch();
  }, refresh_time_out);

  const handleChangeCurrentPage = useCallback(
    (currentPage: number) =>
      runFetch({
        ...previousParams,
        current_page: currentPage,
        page_step: pagination,
      }),
    [pagination, previousParams, runFetch]
  );

  return (
    <>
      {visible && handleDelete && (
        <ConfirmationModal
          visible={visible}
          onConfirm={handleDelete}
          onClose={handleClose}
        />
      )}

      <Table
        dataSource={data}
        columns={columns}
        loading={loading}
        onAdd={handleAdd}
        onEdit={handleEdit}
        onDelete={handleOpen}
        canAdd={canAdd && (usePrivilege ? can_add : true)}
        canDelete={canDelete && (usePrivilege ? can_delete : true)}
        canEdit={canEdit && (usePrivilege ? can_edit : true)}
        canPrint={canPrint && (usePrivilege ? can_print : true)}
        pagination={totalRecord}
        step={pagination}
        onChangeCurrentPage={handleChangeCurrentPage}
        {...tableProps}
      />
    </>
  );
};

// @ts-ignore ignore react "forwardRef" for misleading types.
export default memo(forwardRef(TableWithApi)) as typeof TableWithApi;
export { default as useTableControlsButtons } from "./hooks/useTableControlsButtons";
export { default as useCreateTableActionRef } from "./hooks/useCreateTableActionRef";
