import { memo } from "react";
import { totalFromArray } from "../../helpers";
import { CellContentWrapper } from "../style";

const TotalCellRender = ({
  align,
  width,
  ellipsis,
  dataIndex,
  calculateAmount,
  dataSource,
}: any) => (
  <>
    <CellContentWrapper
      display="block"
      align={align}
      width={width}
      ellipsis={ellipsis ? "true" : undefined}
      disableTranslation
    >
      {calculateAmount && totalFromArray(dataSource, dataIndex)}
    </CellContentWrapper>
  </>
);

export default memo(TotalCellRender);
