import { useState, useCallback, useMemo, useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";
import LoadingOverLay from "../loading-overlay";
import { useBoundingClientRect } from "../hooks";
import { Text } from "../page-title";
import Flex from "../flex";
import createNotification from "../notification";
import Pagination from "../paginator"
import { arrayHasData } from "../helpers"
import TableControlButtons from "./partials/TableControlButtons";
import generateFixedColumns from "./helpers/generateFixedColumns";
import TableHeader from "./partials/TableHeader";
import TableBody from "./partials/TableBody";
import FloatingLabelsTotalCells from "./partials/FloatingLabelsTotalCells";
import createExcelFunction from "./helpers/createExcelFunction";
import { TableContainer, TableContentWrapper, StyledTable } from "./style";
import { TableProps, RecordWithAnyData } from "../types";

const Table = ({
  dataSource,
  height,
  fixedHeight,
  rowKey = "rowKey",
  columns,
  hideTools = true,
  canEdit = false,
  canAdd = false,
  canDelete = false,
  canExcel = false,
  onAdd,
  onEdit,
  onDelete,
  actionColumn,
  actionLabel = "",
  actionWidth,
  onSelectedRow,
  canPrint = false,
  onPrint,
  additionalButtons,
  onSave,
  canSave = false,
  withInfo = true,
  onInfo,
  width = "100%",
  margin,
  padding,
  loading = false,
  onDoubleClick,
  selectedRowBackgroundColor = "lightBlue",
  addDisabled,
  editDisabled,
  deleteDisabled,
  saveDisabled,
  printDisabled,
  excelDisabled,
  overflowY,
  noPagination = false,
  useFloatingLabelsTotalCells,
  pagination = 0,
  step = 0,
  onChangeCurrentPage
}: TableProps) => {
  const { pathname } = useLocation();
  const [clickedRowKey, setClickedRow] = useState();
  const [selectRow, setSelectedRow] = useState<RecordWithAnyData>({});
  const isArrayHasData = arrayHasData(dataSource);
  const [currentPage, setCurrentPage] = useState(1)

  const computedDataSource = step ? dataSource?.slice((currentPage - 1) * step, (currentPage) * step) : dataSource
  const actualData = arrayHasData(computedDataSource) ? computedDataSource : dataSource

  useLayoutEffect(() => {
    if (loading) {
      setClickedRow(undefined);
    }
  }, [loading]);

  const onRowClick = useCallback(
    //@ts-ignore
    (currentRowKey, currentRecord, recordIndex) => () => {
      setClickedRow(currentRowKey);
      onSelectedRow?.(currentRecord, recordIndex);
      setSelectedRow(currentRecord)
    },
    [onSelectedRow]
  );

  const handleDouble = useCallback(
    //@ts-ignore
    (currentRowKey, currentRecord, recordIndex) => () => {
      setClickedRow(currentRowKey);
      onDoubleClick?.(currentRecord, recordIndex);
    },
    [onDoubleClick]
  );

  const pathName = pathname.replace("/", "");
  const excelFun = createExcelFunction({
    fileName: `${pathName} ${new Date().toUTCString()}.xlsx`,
    dataSource: dataSource,
    columns: columns,
  });

  const tableColumnsLength = columns.length;

  const [elementRef, rect] = useBoundingClientRect([
    tableColumnsLength,
    loading,
  ]);

  const containerWidthNumber = rect?.width ?? 200;

  const { adjustedColumns } = useMemo(
    () =>
      generateFixedColumns({
        containerWidthNumber,
        columnsFromProps: columns,
      }),
    [containerWidthNumber, columns]
  );

  const handleInfo = useCallback(() => {
    const { created_at, updated_at } = selectRow
    created_at && createNotification('info', `Create At ${created_at} Updated At ${updated_at}`, '', 5)
  }, [selectRow]);

  const handleChangeCurrentPage = useCallback((page: number) => {
    setCurrentPage(page);
    onChangeCurrentPage && onChangeCurrentPage(page)
  }, [onChangeCurrentPage])

  return (
    <>
      <TableContainer
        width={width}
        padding={padding}
        margin={margin}
        ref={elementRef}
      >
        <TableControlButtons
          hideTools={hideTools}
          canAdd={canAdd}
          canEdit={canEdit}
          canDelete={canDelete}
          canSave={canSave}
          canPrint={canPrint}
          canExcel={canExcel}
          onAdd={onAdd}
          onEdit={onEdit}
          onDelete={onDelete}
          onSave={onSave}
          onPrint={onPrint}
          onExcel={excelFun}
          addDisabled={addDisabled}
          editDisabled={editDisabled || !clickedRowKey}
          deleteDisabled={deleteDisabled || !clickedRowKey}
          saveDisabled={saveDisabled}
          printDisabled={printDisabled}
          excelDisabled={excelDisabled}
          additionalButtons={additionalButtons}
          withInfo={withInfo}
          infoDisabled={!clickedRowKey}
          onInfo={onInfo || handleInfo}
        />
        <TableContentWrapper
          height={height}
          overflowY={overflowY}
          fixedHeight={fixedHeight}
        >
          <StyledTable cellSpacing={0}>
            <TableHeader
              columns={adjustedColumns}
              actionColumn={actionColumn}
              actionLabel={actionLabel}
              actionWidth={actionWidth}
            />

            <TableBody
              columns={adjustedColumns}
              actionColumn={actionColumn}
              dataSource={actualData}
              rowKey={rowKey}
              handleSelectedRow={onRowClick}
              onDoubleClick={handleDouble}
              selectedRowBackgroundColor={selectedRowBackgroundColor}
              clickedRowKey={clickedRowKey}
            />
          </StyledTable>
          {isArrayHasData &&
            useFloatingLabelsTotalCells && (
              <tfoot>
                <FloatingLabelsTotalCells
                  dataSource={dataSource}
                  columns={adjustedColumns}
                />
              </tfoot>
            )}
        </TableContentWrapper>
        {isArrayHasData &&
          !noPagination &&
          <Pagination
            currentPage={currentPage}
            totalRecord={pagination}
            onPageChange={handleChangeCurrentPage}
            step={step}
          />}
        {!isArrayHasData &&
          !fixedHeight && (
            <Flex width="100%" justifyContent="center">
              <Text title="ntd" color="red" fontWeight="bold" />
            </Flex>
          )}
        {loading && <LoadingOverLay visible={loading} />}
      </TableContainer>
    </>
  );
};

export default Table;
