import { notification } from "antd";

const createNotification = (
  type: "success" | "info" | "warning" | "error",
  message?: string,
  description?: string,
  persist: boolean | number = false
) => {
  let msg = "";

  switch (type) {
    case "success":
      msg = "Done Successfully";
      break;

    case "error":
      msg = "False Save";
      break;
  }

  notification[type]({
    message: message || msg,
    description: description ? description : "",
    duration: persist === true ? 0 : ~~(persist || 2.5),
  });
};

export default createNotification;
