import { memo } from "react";
import Flex from "../../flex";
import Button from "../../button";
import IconButton from "../../icon-button";
import { tableControlButtonsProp } from "../../types";

const TableControlButtons = ({
  hideTools = false,
  canAdd,
  canEdit,
  canDelete,
  canSave,
  canPrint,
  canExcel,
  onAdd,
  onEdit,
  onDelete,
  onSave,
  onPrint,
  onExcel,
  addDisabled,
  editDisabled,
  deleteDisabled,
  saveDisabled,
  printDisabled,
  excelDisabled,
  additionalButtons,
  withInfo,
  onInfo,
  infoDisabled
}: tableControlButtonsProp) => (
  <Flex
    width="100%"
    padding="0"
    margin="5px 0"
    // bordered
    borderColor="red"
    justifyContent="center"
    hidden={hideTools}
    gap="10px"
  >
    {additionalButtons &&
      Array.isArray(additionalButtons) &&
      additionalButtons.map((record) => {
        if (record.icon) {
          return (
            <IconButton
              //@ts-ignore
              iconName={record.icon}
              onClick={record.onClick}
              width={record.width}
              backGround="transparent"
            />
          );
        } else {
          return (
            <Button
              label={record.label}
              onClick={record.onClick}
              width={record.width}
            />
          );
        }
      })}
    <IconButton
      iconName="edit"
      onClick={onEdit}
      hidden={!canEdit}
      disabled={editDisabled}
    />
    <IconButton
      iconName="delete"
      onClick={onDelete}
      hidden={!canDelete}
      disabled={deleteDisabled}
    />
    <IconButton
      iconName="plus"
      onClick={onAdd}
      hidden={!canAdd}
      disabled={addDisabled}
    />
    <IconButton
      iconName="save"
      onClick={onSave}
      hidden={!canSave}
      disabled={saveDisabled}
    />
    <IconButton
      iconName="print"
      onClick={onPrint}
      hidden={!canPrint}
      disabled={printDisabled}
    />
    <IconButton
      iconName="excel"
      onClick={onExcel}
      hidden={!canExcel}
      disabled={excelDisabled}
    />
    <IconButton
      iconName="info"
      onClick={onInfo}
      disabled={infoDisabled}
      hidden={!withInfo}
    />
  </Flex>
);

export default memo(TableControlButtons);
